<template>
  <div>
    <b-alert show variant="primary" style="width: 40rem">Primary Alert</b-alert>
    <b-card
    title="LOGIN"
    style="max-width: 40rem;"
    class="mb-2"
    >
    <b-card-text>
      <b-row class="my-1">
        <b-col sm="2">
          <label>아이디:</label>
        </b-col>
        <b-col sm="10">
          <b-form-input v-model="id" size="sm" placeholder="아이디를 입력해주세요."></b-form-input>
        </b-col>
        <b-col sm="2">
          <label>비밀번호:</label>
        </b-col>
        <b-col sm="10">
          <b-form-input v-model="password" size="sm" placeholder="비밀번호를 입력해주세요."></b-form-input>
        </b-col>
      </b-row>
      <b-button @click="login({id, password})">LOGIN</b-button>
    </b-card-text>
  </b-card>
  </div>
</template>
<script>
import { mapActions } from 'vuex'

export default {
  data () {
    return {
      id: '',
      password: ''
    }
  },
  methods: {
    ...mapActions(['login'])
  }
}
</script>



